import React from 'react'
import './App.sass'
import image from '../public/pepe.jpg'
import { useTestQuery } from './store/api/api'

export function App () {
	const { isLoading, isFetching, data } = useTestQuery(null)
  	return (
		<div className='app'>
			<div>Url asset test:</div>
			<div className='pepe-element' />
			<div>Import asset test:</div>
			<img className='pepe-image' src={ image } />
			<div>
				Message from server: {
					isLoading || isFetching ? '...': data.message
				}
			</div> 
		</div>
  	)
}