import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react'

const api = createApi({
	reducerPath: 'api',
	baseQuery: fetchBaseQuery({
		baseUrl: 'https://rokashkov.ru:5000/'
	}),
	endpoints: (builder) => {
		return {
			test: builder.query<any, string | null>({
				query: (search: string) => ({
					url: search
				})
			})
		}
	}
})

const { useTestQuery } = api

export { api, useTestQuery}